<template>
	<div class="healthy">
		<Titlebar />

		<div class="carousel mb">
			<img :src="carousel">
		</div>

		<div class="healthy-list">
			<List :data="healthyData" @changeItem="changeItem" />
		</div>
	</div>
</template>

<script>
	import Titlebar from '@/components/Titlebar.vue'
	import List from '@/components/List.vue'

	export default {
		components: { Titlebar, List },
		data(){
			return {
				healthyData: [],
				carousel: `${process.env.BASE_URL}images/healthy/healthy_banner.png`,
			}
		},
		mounted() {
			fnHealthy(this);
			this.$emit('handlerTabbar', false);
		},
		methods: {
			changeItem(item) {
				this.$router.push({ path: 'healthy-detail', query: { id: item.id }});
			}
		}
	}

	function fnHealthy(that, params) {
		that.$request.healthy(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.healthyData = result.list;
			}
		})
	}
</script>

<style lang="scss">
	@import './../../styles/healthy/healthy.scss';
</style>